import React, {Component} from "react";
import PropTypes from "prop-types";

window.tooltipId = ((window.tooltipId != undefined) ? window.tooltipId + 1 : 1);

class OraTooltip extends Component {
    constructor(props) {
        super(props);
    }

    stopModule() {
        OPL.UI.stopModules(this.refWrapper);
    }

    componentWillMount() {
        const baseId = (this.props.baseId && (this.props.baseId + "_" + window.tooltipId++)) || window.tooltipId++;
        const id = this.props.id || baseId;
        this.tooltipId = "rTip-" + baseId;
        this.triggerId = "rTip-" + id + "-trigger";
        this.contentClass = "rTip-" + id + "-content";
    }

    componentDidMount() {
        this.loadModule();
    }

    componentWillUpdate() {
        this.stopModule();
    }

    componentDidUpdate() {
        this.loadModule();
    }

    loadModule() {
        OPL.UI.loadModules(this.refContainer, [
            {
                path: "core/modules/tooltips",
                options:
                    {
                        triggerSelector: ".o-tooltip__trigger",
                        contentSelector: ".o-tooltip__content",
                        maxWidth: this.props.maxWidth,
                        triggerEvent: "hover",
                        side: "top"

                    },
            },
        ])
    }

    componentWillUnmount() {
        this.stopModule();
    }

    onClick = event => {
        console.warn("tooltip prevent", event);
        event.preventDefault();
    };

    renderContent() {
        let children = this.props.children;
        if (typeof (children) != "string") {
            return (<div id={this.tooltipId}
                         className={"u-hide--soft o-tooltip__content " + this.contentClass}
                         role="tooltip"
            >
                {this.props.children}
            </div>)
        } else {
            return (<div id={this.tooltipId}
                         className={"u-hide--soft o-tooltip__content " + this.contentClass}
                         role="tooltip"
                         dangerouslySetInnerHTML={{__html: this.props.children}}
            ></div>)

        }

    }

    render() {
        if (this.props.labelSpan)
            return (
                <span className={this.props.className} id={"tooltip-container_wrapper_" + this.tooltipId}
                      ref={ref => this.refWrapper = ref} onClick={this.onClick}>
                    <span className="u-inline" id={"tooltip-container-" + this.tooltipId}
                          ref={ref => this.refContainer = ref}>

                        {
                            <span data-type="no-click" data-tooltip-trigger-event="mouseover"
                                  className={"o-tooltip__trigger   tooltipstered "}>
                        {this.props.labelSpan}
                        </span>}
                        {this.renderContent()}
                    </span>
                </span>


            )

        return (
            <div className={this.props.className} id={"tooltip-container_wrapper_" + this.tooltipId}
                 ref={ref => this.refWrapper = ref} onClick={this.onClick}>
                <div className="u-inline" id={"tooltip-container-" + this.tooltipId}
                     ref={ref => this.refContainer = ref}>

                    {!!this.props.labelBlock &&
                    <div data-type="no-click" data-tooltip-trigger-event="mouseover"
                         className={"o-tooltip__trigger   tooltipstered "}>

                        {this.props.labelBlock}
                    </div>
                    }
                    {!(!!this.props.labelBlock) &&
                    [this.props.label,
                        <a key="link" href="#" data-type="no-click" data-tooltip-trigger-event="mouseover"
                           className={"o-tooltip__trigger o-tooltip--top g-black1-c tooltipstered " + this.props.iconClassName}>
                            <span className="g-icon g-icon--font g-icon--hint u-padding-left-s"/>
                        </a>
                    ]
                    }
                    {this.renderContent()}
                </div>
            </div>
        )
    }
}


OraTooltip.propTypes = {
    open: PropTypes.bool,
    className: PropTypes.string,
    iconClassName: PropTypes.string,
    maxWidth: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    label: PropTypes.node
};

OraTooltip.defaultProps = {
    className: "u-inline-block",
    iconClassName: "",
    maxWidth: "300",
};

export default OraTooltip;
